<template>
  <div class="register">
    <van-nav-bar
      left-text="返回"
      title="登录"
      right-text="注册"
      @click-left="goPage('Home')"
      @click-right="goPage('Register')"
      left-arrow
    />
    <div class="logo">
      <img class="auto-img" src="../assets/images/logoBiger.png" alt />
    </div>
    <van-form @submit="login">
      <van-field v-model="userInfo.phone" type="text" label="手机号" placeholder="手机号" maxlength="11">
        <template v-slot:left-icon>
          <i class="fa fa-phone"></i>
        </template>
      </van-field>
      <van-field
        v-model="userInfo.password"
        left-icon="closed-eye"
        :type="text"
        label="密码"
        placeholder="密码"
        :right-icon="bool ? 'closed-eye' : 'eye-o'"
        @click-right-icon="open"
        maxlength="16"
      >
        <template v-slot:left-icon>
          <i class="fa fa-lock"></i>
        </template>
      </van-field>

      <div class="register-btn">
        <van-button round block type="info" native-type="submit">立即登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
//导入验证表单模块
import validForm from "../assets/js/ValidForm";
export default {
  data() {
    return {
      userInfo: {
        phone: "",
        password: ""
      },
      text: "password",
      bool: true
    };
  },
  methods: {
    forgot() {
      this.$router.push({ name: "Forgot" });
    },
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    open() {
      if (!this.bool) {
        this.text = "password";
        this.bool = true;
      } else {
        this.text = "text";
        this.bool = false;
      }
    },
    //登录
    login() {
      //进行验证表单
      if (validForm.validUserForm(this.userInfo, this)) {
        //复制对象
        let data = Object.assign({}, this.userInfo);
        //请求密钥
        data.appkey = this.appkey;
        //开启加载提示
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });
        //发起注册请求
        this.axios({
          method: "POST",
          url: "/login",
          //post传递参数需要写在data
          data
        })
          .then(result => {
            //关闭加载提示
            this.$toast.clear();
            console.log(result.data);
            if (result.data.code == 200) {
              this.$router.go(-1);
              //存储token，用于后面登录验证
              localStorage.setItem("bookTK", result.data.token);
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            //关闭加载提示
            this.$toast.clear();
          });
      }
    }
  }
};
</script>
